body * {
    margin: 0;
    padding: 0;
}
header {
    background: brown;
    color: #fff;
}
.buttons-title {
    display: inline-block;
    padding-right: 1rem;
    text-align: right;
    width: 225px;
}
.container-table {
    max-width: 100%;
    margin-left: 1.5rem;
    position: relative;
}

.table-scroll {
    overflow-x: auto;
    margin-left: 75px;
}

.fixed {
    position: absolute;
    left: 0;
    padding: 0 50px;
    width: 100px;
}
